*{
	margin:0;
}
.Markdownie {
	display: flex;
	width:100vw;
	height: 100vh;
	color: white;
	
}

.Markdownie>textarea{
	width: 50%;
	font-size: 15px;
	border: none;
	background-color:#555866;
	color: white;
}
.Markdownie__div--displayArea{
	padding: 10px;
	width: 50%;
	background-color: #3c3e47;
	font-family: Arial, Helvetica, sans-serif;
	line-height: 30px;
}
.Markdownie__div--menu{
	position: absolute;
	background-color: #5f616b;
	padding: 10px;
	border-radius: 10%;
	right:10px;
	top :10px;
	transition: 0.1s;

}
.Markdownie__div--dropDown{
	display :none;
	position: absolute;
	z-index: 2;
	right: 10px;
	top: 30px;
	background-color: black;
	padding: 10px;
	border-radius: 5%;
	
}
.Markdownie__div--dropDown>div{
	color: white;
	padding-top: 10px;
	padding-bottom: 10px;
	padding-left: 5px;
	padding-right: 10px;
	border-radius: 10px;
	transition: 0.1s;

}
.Markdownie__div--dropDown>div:hover{
	background-color: cyan;
	color: black;

}

.Markdownie__div--menu:hover .Markdownie__div--dropDown {
	display: block;
}
.Markdownie__div--menu:hover  {
	background-color: black;
}
